@import "../../styles/fonts";
@import "../../styles/colors";

.projectContent {
	box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

	.projectTitle {
		text-align: center;
		padding: 30px 0 20px 0;
		font-family: $font-primary;
		font-size: 36px;
	}

	.projectUrl:link, .projectUrl:visited {
		margin: 20px 10px 10px 10px;
		background-color: $primary;
		color: white;
		padding: 14px 0;
		text-align: center;
		text-decoration: none;

		img {
			width: 20px;
			height: 20px;
			margin: 0 10px;
		}
	}
	
	.projectUrl:hover, .projectUrl:active {
		background-color: $accent;
		color: black;
	}

	.projectDescription {
		text-align: left;
		padding: 10px 0 ;
		font-family: $font-tertiary;
		font-size: 18px;
		letter-spacing: 0.1rem;
		text-align: justify;
		line-height: 24px;
	}
	.projectDescription:nth-of-type(even) {
		padding: 0 0 20px 0;
	}

	.projectPhoto {
		object-fit: cover;
		padding: 10px 0;
	}

	@media (max-width: 1000px) {
		.projectPhoto {
			padding: 10px 0;
			margin: 0;
			width:80vw;
		}

		.projectUrl {
			padding: 14px 28px !important;
		}
	}
}